body{
  margin:0;
  overflow-x: hidden;
  font-size: 12px;

}

*,
*::before,
*::after { 
    margin: 0; 
    padding: 0; 
    box-sizing: border-box; 
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    color: white;
    overflow-x: hidden;

}





#home {
  height: 100vh;
  width: 100vw;
  overflow-y: hidden;
  background: linear-gradient(45deg, #00FFBA, #6890D8, #2c67d4, #9906a3);
  background-repeat: no-repeat;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  z-index: 2;
  position: relative;

}

.rightImage {
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  display: flex;
  justify-content: right;
  top: 8vh;
  position: absolute;
  right: 0;

}

.rightImage img{
  transform: translatey(0px);
  animation: float 6s ease-in-out infinite;
  z-index: 1;
  display: inline;
  max-width: 90vw;
  max-height: 90vh;

}

.potatoInfo{
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  display: flex;
  justify-content: left;
  top: 10vh;
  position: absolute;
  left: 5vw;
  flex-direction: column;
}

.potatoInfo h1{
  max-width: 80%;
  width: 80%;
  margin-top: 8vh;
  font-size: 4em;
}

.potatoInfo h2{
  max-width: 80%;
  width: 80%;
  margin-top: 4vh;
  font-size: 2em;
}

.potatoInfo p{
  max-width: 80%;
  width: 80%;
  margin-top: 20vh;
  font-size: 2em;
}

.potatoInfo a{
  margin-top: 5vh;
  font-size: 2em;
  margin-top: 5vh;
  text-decoration: 0;
  color: white;
  border: 2px solid white;
  border-radius: 50px;
  padding: 20px;
  max-width: 170px;
}


.nav{
  z-index: 3;
  background: linear-gradient(45deg, #00FFBA, #6890D8, #2c67d4, #9906a3);
  animation: gradient 10s ease infinite;
  background-repeat: no-repeat; 
  width: 100%;
  top: 10px;
}

.nav ul {
  list-style-type: none;
  margin: 0;
  padding: 10px;
  overflow: hidden;
  display: flex;
  justify-content: left;
  
}

.nav li {
  display: inline-block;
  justify-content: space-around;
  margin-right: 50px;  
}

.nav li a {
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 2em;
}

.nav ul li a:hover {
  color: #9906a3;
}

.nav ul li a:visited {
  text-decoration: none;
}



#buypage {
  background-color: #00FFBA;
  height: 100vh;
  width: 100vw;
  z-index: 3;
  overflow-y: hidden;
  margin: 0;
  padding:0;
  text-align: center;
  overflow-x: hidden;

}

.container { 
  -moz-column-count:2;
  -webkit-column-count:2;
  column-count:2;
  height: 100vh;
  width: 100vw;
}

.block{
  padding-top: 10vh;
}


.container img{
  max-width: 100%;
}


#buypage h1{
  margin-top: 8vh;
  font-size: 3.5em;
}



#community {
  width: 100vw;
  height: 100vh;
  background-color: burlywood;
  display: flex;
  flex-direction: column;
}


.Social{
  overflow: hidden;
  z-index: 0;
  display: flex;
  justify-content: left;
  flex-direction: column;
  position: absolute;
  margin-top: 5vh;
  right: 0;

}

.Social img{
  z-index: 1;
  max-width: 30vw;
  max-height: 30vh;
}


.leftImage {
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  justify-content: left;

}

.leftImage img{
  z-index: 1;
  display: inline;
  max-width: 90vw;
  max-height: 100vh;
  margin-top: 30vh;
}





footer {
  background-color: rgb(66, 54, 38);
}



footer {
  background-color: rgb(66, 54, 38);
  color: white;
  padding: 30px;
  text-align: center;
}



@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@-webkit-keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@keyframes color-change {
  0% {
      color: #9906a3;
  }
  50% {
      color: #6890D8;
  }
  100% {
      color: #00FFBA;
  }
  50% {
      color: #d868ad;
  }
}

@-webkit-keyframes color-change {
  0% {
      color: #9906a3;
  }
  50% {
      color: #6890D8;
  }
  100% {
      color: #00FFBA;
  }
  50% {
      color: #d868ad;
  }
}

@keyframes float {
  0% {
      transform: translatey(0px);
  }
  50% {
      transform: translatey(-100px);
  }
  100% {
      transform: translatey(0px);
  }
}









@media only screen and (max-width: 1100px) {

  .rightImage img{
    max-height: 40vh;
  
  }

  .rightImage {
    padding-top: 100px;
    top: 30vh;
    max-height: 100vh;
  
  }

  .potatoInfo h1{
    margin-top: 4vh;
    font-size: 3em;
  }
  
  .potatoInfo h2{
    font-size: 1.5em;
  }
  
  .potatoInfo p{
    font-size: 1.5em;
  }
  
  .potatoInfo a{
    font-size: 2em;
  }
  
  
.nav{
  font-size: 2em;
}

.container { 
  -moz-column-count:1;
  -webkit-column-count:1;
  column-count:1;
}

.container img{
  max-width: 100vw;
  max-height: 20vh;
}

.block{
  padding-top: 0vh;
}


.Social{
  margin-top: 50vh;
}

.Social img{
  max-width: 10vw;
  max-height: 30vh;
}



.leftImage img{
  z-index: 1;
  display: inline;
  max-width: 100vw;
  max-height: 100vh;
  margin-top: 20vh;
}


}


@media only screen and (max-width: 600px) {

  .Social{
    overflow: hidden;
    z-index: 0;
    display: flex;
    justify-content: center;
    flex-direction: row;
    position: relative;
    margin-top: 1vh;
    position: absolute;
    margin-top: 10vh;
  }
  
  .Social img{
    z-index: 1;
    max-width: 33vw;
    max-height: 50vh;
  }
  
  
  .leftImage {
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    justify-content: left;
  
  }
  
  .leftImage img{
    z-index: 1;
    display: inline;
    max-width: 100vw;
    max-height: 100vh;
    margin-top: 70vh;
  }


}









@media only screen and (max-width: 400px) {
  .rightImage img{
    max-height: 30vh;
  
  }

  .rightImage {
    padding-top: 100px;
    top: 50vh;
    max-height: 100vh;
  
  }

  .potatoInfo h1{
    margin-top: 1vh;
    font-size: 2.5em;
  }
  
  .potatoInfo h2{
    font-size: 1.5em;
  }
  
  .potatoInfo p{
    font-size: 1.5em;
  }
  
  .potatoInfo a{
    font-size: 2em;
  }

  .nav{
    font-size: 1.5em;
  }

  .nav li a {
    display: block;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 1em;
  }


  #buypage h1{
    margin-top: 8vh;
    font-size: 2.5em;
  }
  
 


}








